export default {
	data() {
		return {

		};
	},
	props: {
		item: Object,
		endTime: String
	},
	computed: {
		isEnd: function() {
			let flog = 1;
			// console.log(this.item.state)
			switch (this.item.state) {
				case "还未开始":
					flog = 2;
					break;
				case "已结束":
					flog = 0;
					break;
				case "进行中":
					flog = 1;
					break;
				default:
					break;
			}
			return flog;
		},
		countdownData: function() {
			return this.$common.getFlashTime(this.endTime);
		}
	}
}
